html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-family: "Montserrat", "Verdana", sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  font-style: normal;
  color: rgba(44, 46, 63, 0.7);
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  color: inherit; }

.contacts {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 32px;
  color: #fe7865;
  text-decoration: underline; }
  .contacts svg {
    margin-right: 10px; }
  .contacts:hover {
    color: #f17d4b;
    opacity: 0.8;
    transition: color opacity 0.2s ease; }
  .contacts:active {
    opacity: 0.5;
    transition: opacity 0.2s ease; }
  .contacts--footer-mail {
    font-size: inherit;
    line-height: inherit; }
  .contacts--footer-phone {
    font-size: 16px; }

.contacts__text--tablet {
  display: none; }

@media (max-width: 1023px) {
  .contacts__text--desktop {
    display: none; }
  .contacts__text--tablet {
    display: inline; } }

.header {
  display: flex;
  align-items: center;
  padding: 39px 19.6% 32px 19.7%;
  color: #2c2e3f; }

.header__menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-left: auto;
  margin-top: -5px;
  margin-right: 13px;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500; }

.header__menu-item {
  margin-right: 40px; }

.header__menu-link {
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  color: #2c2e3f; }
  .header__menu-link:hover {
    color: #fe7865;
    transition: color 0.2s ease; }
  .header__menu-link:active {
    color: #f17d4b;
    opacity: 0.5;
    transition: opacity 0.2s ease; }

.header__menu-icon {
  display: none; }

.header__phone {
  margin-top: -4px; }

.header__menu-button {
  display: none; }

@media (max-width: 1023px) {
  .header {
    position: relative;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0; }
  .header__logo {
    margin-left: 21px;
    width: 44px;
    height: 17px; }
  .header__menu {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    flex-direction: column;
    width: 100%;
    margin: 0;
    padding: 97px 0;
    background-color: #ffffff; }
    .header__menu--open {
      display: flex; }
    .header__menu--nojs {
      display: flex;
      position: relative;
      order: 10;
      padding-top: 47px; }
  .header__menu-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    margin-bottom: 30px;
    padding: 15px 16px 17px;
    background-color: #fbf8f8; }
  .header__menu-link {
    font-size: 20px;
    line-height: 28px;
    font-weight: 600; }
  .header__menu-icon {
    display: block;
    margin: 10px 20px; }
  .header__phone {
    display: none; }
  .header__menu-button {
    display: block;
    padding: 17px 25px 19px;
    background-color: #fe7865;
    border: none; }
    .header__menu-button--open {
      z-index: 10;
      padding: 15px 20px;
      background-color: transparent; }
    .header__menu-button--nojs {
      display: none; }
  .header__icon {
    display: none; }
    .header__icon--active {
      display: block; } }

@media (max-width: 767px) {
  .header__logo {
    margin-left: 16px; }
  .header__menu {
    padding-top: 134px;
    padding-bottom: 254px; }
  .header__menu-button {
    padding: 17px 14px 19px; }
    .header__menu-button--open {
      padding: 15px 14px 16px; }
  .header__menu-icon {
    margin: 8px; } }

.main {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 183px 22.6% 295px 56.5%;
  color: #ffffff;
  background-image: url("../img/start-bg-desktop.jpg");
  background-repeat: no-repeat;
  background-position: -148px -671px; }

.main__title {
  width: 398px;
  margin: 0;
  margin-bottom: 29px;
  font-size: 55px;
  line-height: 80px;
  font-weight: 600;
  letter-spacing: -1.2px; }

.main__intro {
  width: 398px;
  margin: 0;
  margin-bottom: 36px;
  padding-right: 20px;
  line-height: 27px;
  letter-spacing: -0.5px; }

.main__button {
  display: flex;
