@media (max-width: 1023px) {
  .main {
    align-items: center;
    padding: 60px 185px 83px;
    background-image: url("../img/start-bg-tablet.jpg");
    background-position: -58px -265px;
    background-size: 116.7% auto; }
  .main__title {
    width: auto;
    margin-bottom: 16px;
    font-size: 30px;
    line-height: 45px;
    letter-spacing: 0; }
  .main__intro {
