  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.countries-list__item {
  max-width: 95px;
  margin-right: 23px;
  margin-bottom: 20px; }
  .countries-list__item:last-child {
    margin-right: 0; }

.countries-list__sublist {
